import './App.css';
import Navbar from './components/Navbar';
//import About from './components/about';
import Textform from './components/Textform';
import React, { useState } from 'react';
import Alert from './components/Alert';
//import { BrowserRouter as Router,Route,Routes} from "react-router-dom";
function App() {
  const [alert,setalert]=useState(null);
  const showalert = (message,type)=>{
    setalert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setalert(null)
    }, 1000);
  }
  const [mode,setMode] = useState('light')
  const toggleMode = ()=>{
    if(mode==='dark'){
      setMode('light');
      showalert("Light Mode Enabled","success");
    }
    else{
      setMode('dark');
      showalert("Dark Mode Enabled","success");
    }
  }
  
  return (
  <>
  <div className={`container text-${mode==='light'?'dark':'light'} bg-${mode}`}><br/><br/>
  {/*<Router>*/}
    <Navbar title = "ShaelSite" mode={mode} toggleMode={toggleMode}/>
    <hr/>
    <Alert alert={alert}/>
  {/*<Routes>*/}
    {/*<Route path="/about" element={<About mode={mode} toggleMode={toggleMode} />} />*/}
    <Textform heading="Enter the text to analyse" showalert={showalert} mode={mode} toggleMode={toggleMode}/>
  {/*</Routes>
  </Router>*/}
  </div>
  </>
  );
}

export default App;
