import React, {useState} from 'react';

export default function Textform(props) {
    const [text,setText] = useState("");
    const handleUpClick = ()=>{
        let a = text.toUpperCase();
        setText(a);
        props.showalert("Converted to UpperCase","success");
    };
    const handleLowClick = () =>{
        let a = text.toLowerCase();
        setText(a);
        props.showalert("Converted to LowerCase","success");
    };
    const handleOnChange = (event)=>{
        console.log("On change");
        setText(event.target.value);
    };
    return (
        <>
        <div className="container1">
        
            <h1>{props.heading}</h1>
            <div className="form-group">
            <textarea className="form-control" value={text} id="exampleFormControlTextarea1" onChange={handleOnChange} rows="8" ></textarea>
            </div><br/>
            <button className="btn btn-primary mx-2" onClick={handleUpClick}>Change To Uppercase</button>
            <button className="btn btn-primary mx-2" onClick={handleLowClick}>Change To Lowercase</button>
        
        <div className="container2 m-3">
        <h1 className='Heading'>Your Text Summary</h1>
        <p>Your text has {text.split(" ").length} words and {text.length} characters</p>
        <p>Total Time needed for Reading: {text.split(" ").length*0.008} minutes</p>
        <h2 className='Heading'>Preview</h2>
        <p>{text}</p>
        </div>
        </div>
    </>
    )
}
